<template>
  <div class="home">
    <!-- 搜索框 -->
    <SearchInput></SearchInput>

    <!-- 轮播图 开始-->
    <van-swipe class="swiper_box" :autoplay="3000" indicator-color="white">
      <van-swipe-item class="swiper" v-for="(swiper,index) in swiperData" :key="index">
        <router-link :to="'/goodsdetail?goods_id='+swiper.goods_id">
          <van-image lazy-load :src="swiper.image_src" />
        </router-link>
      </van-swipe-item>
    </van-swipe>
    <!-- 轮播图 结束-->

    <!-- 导航区 -->
    <div class="catitems_box">
      <div class="catitem_item" v-for="(catitem,index) in catitemsData" :key="index">
        <router-link to>
          <van-image :src="catitem.image_src" />
        </router-link>
      </div>
    </div>

    <Floor :floors="floorData"></Floor>
    <FootTab></FootTab>
  </div>
</template>

<script>
import { reaxios } from "@/assets/js/reaxios";
import "@/assets/css/home.css";
import SearchInput from "../components/SearchInput";
import Floor from "../components/Floor";
import FootTab from "../components/FootTab";
export default {
  data() {
    return {
      swiperData: [],
      catitemsData: [],
      floorData: []
    };
  },
  created() {
    this.getData();
  },
  methods: {
    /* 获取数据 */
    getData() {
      // 轮播图数据
      reaxios(0, { url: "/home/swiperdata" }).then(result => {
        this.swiperData = result.data.message;
      });

      // 导航区数据
      reaxios(0, { url: "/home/catitems" }).then(result => {
        this.catitemsData = result.data.message;
      });

      // 楼层数据
      reaxios(0, { url: "/home/floordata" }).then(result => {
        this.floorData = result.data.message;
      });
    }
  },

  // 自定义组件注册
  components: { SearchInput, Floor, FootTab }
};
</script>


